.yc-row, .yc-title-col, .yc-hr{
    margin-top: 0px;
    margin-bottom: 0px;
    padding-top: 0px;
    padding-bottom: 0px;
}

.yc-btn-order{
    background:-webkit-gradient(linear, left top, left bottom, from(#E4024A),to(#CE0552));
    background:-webkit-linear-gradient(top, #E4024A 0%,#CE0552 100%);
    background:-o-linear-gradient(top, #E4024A 0%,#CE0552 100%);
    background:linear-gradient(to bottom, #E4024A 0%,#CE0552 100%);
    text-align: center;
}

.yc-btn-p{
    color: white;
}

.yc-cancel-col,.yc-cancel{
    color:#CE0552;
    text-align: right;
    padding-right:0px;
}

.yc-copyright{
    font-size: 12px;
    color:#B3B2B2;
}

.yc-copyright-a{
    color: #CE0552;
}

.yc-grid-container{
    background: white;
    margin-top: 5%;
    margin-bottom: 5%;
}

.yc-h1title-order, .yc-title-col{
    font-weight: bold;
}

.yc-grid-container-image{
    background-image: url(images/sushi-preview.png);
    background-size: cover;
    background-repeat: no-repeat;
    padding-left:0px;
    padding: right;
}

.yc-grid-container-final{
    padding: 5%;
}


